<template>
  <div id="app" >
  <nav class="navbar navbar-expand-md navbar-light bg-white fixed-top ">
    <router-link to="/" class="navbar-brand"><img src="/logo.png" class="img img-fluid" style="width:134px;height:auto"> 
    Assessment</router-link>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarCollapse">
      <ul class="navbar-nav mr-auto text-center">
         <li class="nav-item"  data-toggle="collapse" data-target=".navbar-collapse.show">
          <router-link to="/home" class="nav-link">Home</router-link>
        </li>
        <li class="nav-item"  data-toggle="collapse" data-target=".navbar-collapse.show">
          <router-link to="/article" class="nav-link">Article</router-link>
        </li>
        <li class="nav-item" v-if="currentUser" >
          <router-link to="/add" class="nav-link">Create</router-link>
        </li>
      </ul>
      <div v-if="!currentUser" class="navbar-nav ml-auto text-center">
        <li class="nav-item"  data-toggle="collapse" data-target=".navbar-collapse.show">
          <a class="nav-link login mr-auto" @click="showModal()">
            Login
          </a>
        </li>
      </div>
      <div v-if="currentUser" class="navbar-nav ml-auto text-center">
        <li class="nav-item">
          <button class="btn btn-outline-success" @click="logOut">
            Log out {{ currentUser.name }}
          </button>
        </li>
      </div>

      
    </div>
    </nav>

    <main role="main">
    <div class="container home">
      <router-view />
    </div>
    <footer class="fixed-bottom">
      <div class="row">
        <div class="col-md-12">Copyright @copy Sahaware Assessment 2021</div>
      </div>
    </footer>
    </main>
    <!-- Modal Login -->
    <div id="ModalLogin" class="modal fade mb-sm-0" role="dialog"  >
      <div class="modal-dialog modal-md ">
        <!-- Modal content-->
        <div class="modal-content p-md-5">
          <div class="modal-header d-flex flex-column">
           <button type="button" class="close d-block d-sm-none" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
            <h4 class="modal-title">Login</h4>
            <p>Don't have an account? <a @click="sign_up" class="text-danger">Create Account</a></p>

          </div>
          <div class="modal-body">
              <Form @submit="handleLogin" :validation-schema="schema">
                <div class="form-group">
                  <label for="email">Username</label>
                  <Field name="email" type="text" placeholder="Entry Your Email" class="form-control" />
                  <ErrorMessage name="email" class="error-feedback" />
                </div>
                <div class="form-group">
                  <label for="password">Password</label>
                  <Field name="password" type="password" placeholder="Entry Your Password" class="form-control" />
                  <ErrorMessage name="password" class="error-feedback" />
                </div>

                <div class="form-group">
                  <button class="btn btn-danger btn-custom" :disabled="loading">
                    <span
                      v-show="loading"
                      class="spinner-border spinner-border-sm"
                    ></span>
                    <span>Log in</span>
                  </button>
                </div>

                <div class="form-group">
                  <div v-if="message" class="alert alert-danger" role="alert">
                    {{ message }}
                  </div>
                </div>
              </Form>
          </div>
          
        </div>
      </div>
 
    </div>
    <!-- End Modal Login -->

    <!-- Modal Signup -->
    <div id="ModalSignup" class="modal fade" role="dialog"  >
      <div class="modal-dialog modal-md ">
        <!-- Modal content-->
        <div class="modal-content p-md-5">
          <div class="modal-header d-flex flex-column">
            <button type="button" class="close d-block d-sm-none" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
            <h4 class="modal-title">Create Account</h4>
            <p>Have an account? <a @click="sign_in" class="text-danger">Login</a></p>
          </div>
          <div class="modal-body">
              <Form @submit="handleSignup" :validation-schema="schemaSignup">
                <div class="form-group">
                  <label for="name">Name</label>
                  <Field name="name" type="text" placeholder="Entry Your Name" class="form-control" />
                  <ErrorMessage name="name" class="error-feedback" />
                </div>
                <div class="form-group">
                  <label for="email">Email</label>
                  <Field name="email" type="text" placeholder="Entry Your Email" class="form-control" />
                  <ErrorMessage name="email" class="error-feedback" />
                </div>
                <div class="form-group">
                  <label for="password">Password</label>
                  <Field name="password" type="password" placeholder="Entry Your Password" class="form-control" />
                  <ErrorMessage name="password" class="error-feedback" />
                </div>
                <div class="form-group">
                  <label for="phone">Phone</label>
                  <Field name="phone" type="text" class="form-control" />
                </div>

                <div class="form-group">
                  <button class="btn btn-danger btn-custom" :disabled="loading">
                    <span
                      v-show="loading"
                      class="spinner-border spinner-border-sm"
                    ></span>
                    <span>Create Account</span>
                  </button>
                </div>

                <div class="form-group">
                  <div v-if="register_message" class="alert alert-danger" role="alert">
                    {{ register_message }}
                  </div>
                  <div v-if="register_message_success" class="alert alert-success" role="alert">
                    {{ register_message_success }}
                  </div>
                </div>
              </Form>
          </div>
          
        </div>
      </div>
 
    </div>
    <!-- End Modal Signup -->
  </div>
</template>

<script>
import { Form, Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";
import { Modal } from "bootstrap";

export default {
  name: "app",
  components: {
    Form,
    Field,
    ErrorMessage
  },
  data() { 
    const schema = yup.object().shape({
      email: yup.string().required("Email is required!"),
      password: yup.string().required("Password is required!"),
    });
    const schemaSignup = yup.object().shape({
      name: yup.string().required("Name is required!"),
      email: yup.string().required("Email is required!"),
      password: yup.string().required("Password is required!"),

    });
    return {
      loading: false,
      message: "",
      register_message:"",
      register_message_success:"",
      schema,
      schemaSignup,
      loginModal: null,
      signupModal: null,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
    currentUser() {
      return this.$store.state.auth.user;
    }
  },
  methods: {
    handleLogin(user) {
      this.loading = true;
      this.$store.dispatch("auth/login", user).then(
        () => {
          this.loginModal.hide();
          this.$router.push("/add");
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
    handleSignup(user) {
      this.loading = true;
      this.$store.dispatch("auth/register", user).then(
        (result) => {
          this.loading = false;
          this.register_message ="";
          this.register_message_success = result.message
        },
        (error) => {
          this.loading = false;
          this.register_message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
    logOut() {
      this.$store.dispatch('auth/logout');
      this.$router.push('/home');
    },
    showModal(){
        this.loginModal = new Modal(document.getElementById('ModalLogin'), {})
        this.signupModal = new Modal(document.getElementById('ModalSignup'), {})
        this.loginModal.show()
    },
    closeModal(obj){
      obj.hide()
    },
    sign_in(){
      this.loginModal.show();
      this.signupModal.hide();
    },
    sign_up(){
      this.signupModal.show();
      this.loginModal.hide();
    }


  }
  
};
</script>

<style>
.navbar{
  background: #FFFFFF;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  padding: 30px 80px !important;
}
.nav-link login {
  color: #2D9CDB !important;
}
.nav-link{
  color:#000 !important;
}
.router-link-active{
  color:#ED3237 !important;
}
.home{
  margin-top:160px;
  margin-bottom:100px
}
footer{
  color:#fff;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 24px 80px;
}

.modal-title{
  font-weight: 700;
  line-height: 40px;
  letter-spacing: 0.0025em;
  text-align: left;

}
.article-list{
  margin:80px 70px !important; 
}
.article-item {
  border:none !important;
}
.btn-danger, footer {
  background-color:#ED3237 !important;
}



@media screen and (max-width: 767px) {
  
  .home{
    margin-top:88px   
  }
  footer{
   padding: 24px 16px;

  }
  .navbar-light .navbar-nav .nav-link{
    font-weight: 400;
    font-size: 29px !important;
    line-height: 21px;
    margin-bottom:10px;
  }
  .navbar{
    box-shadow: none;
    padding: 15px 30px !important;
  }
  #navbarCollapse{
    height:1000px !important;
  }
  h1{
    font-size:24px !important;
  }
  .modal-dialog{
    margin: auto !important;
  }
  .modal{
      top:unset !important;
      left:unset !important;
      height:unset !important;
      bottom: 0 !important;
  }
  .btn-custom{
      width: 100%; 
      display:block;
  }
  .article-list{
    margin:30px 30px !important; 
  }
}
</style>
